.contributions__filter{
	display: block;
	margin:0 0 41px;
	border: 1px solid rgba(133, 133, 133, 0.1);
	@media screen and (max-width: 575px){
		border: none;
		margin: 0px;
	}
}
.second__title-settlement-service.contributions_title{
	@media screen and (max-width:991px){
		display: none;
	}
}
.contribuitions__second-title{
	font-family: 'Helvetica';
	font-style: normal;
	font-weight: 700;
	font-size: 18px;
	line-height: 21px;
	color: #003772;
	margin-top: 32px;
}
.contributions__wrapper{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin: 40px 0px;
	@media screen and (max-width: 1199px){
		margin: 40px 20px;
	}
	@media screen and (max-width:575px){
		display: block;
		margin: 40px 10px;
	}
}
.filter__option-form.contributions__form{
	align-items: stretch;
	width: -webkit-fill-available;
}
.sliderBar.contributions__bar{
	align-self: center;
}
.contributions__bar-text{
	margin-right: 5px;
}
.contributions__bar-amount{
	margin-left: 5px;

}
.filter__item.contributions__item{
	margin-left: 0px;
	margin-right: 0px;
}
.filter__item.contributions__item:first-child{
	margin-right: 8px;
}
.filter__item.contributions__item:not(:last-child){
	margin-bottom: 25px;
}
.filter__contributions-cheks{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-self: center;
	flex: 1 1 41%;
	margin:0 0px 30px 0px;
	@media screen and (max-width: 1440px){
		flex: 1 0 40%;
	}
	// @media screen and (max-width: 1199px){
	//     margin:0 0px 30px 0px;
	// }
	 @media screen and (max-width:575px){
		display: block;
	}
}

.btn.btn-filter.contributions-btn{
   @media screen and (max-width: 575px){
		width: -webkit-fill-available;
}    
} 
.contributions-type__picture img{
	@media screen and (max-width: 1199px){
		object-fit: cover;
	width: 213px;
	height: 309px;
	}
	
}
.contributions-type {
	display: flex;
	
	justify-content: center;
	margin:0 0 41px;
	background: #FFFFFF;
	border: 1px solid #D1D1D1;
	box-shadow: 0px 4px 4px rgba(0, 32, 91, 0.15);
	@media screen and (max-width:767px){
		margin: 0 0 41px;
	}
		// .contributions-type__picture

		&__picture {
			flex: 0 0 35%;
			@media screen and (max-width:1199px){
				width: 213px;
			}
			@media screen and (max-width:767px){
				display: none;
			}
			.contributions__picture{ 
				
			}
		}

		// .contributions-type__content

		&__content {
			display: grid;
			align-items: center;
			grid-template-columns: 0.7fr 0.5fr 0.2fr 0.5fr 0.3fr 0.7fr ;
			grid-template-rows: 1fr 1fr 1fr 1fr;
			margin:15px;
			column-gap: 7px;
			@media screen and (max-width:1440px){
				margin:30px 10px
			}
			@media screen and (max-width: 1199px){
				grid-template-columns: 0.5fr 0.2fr 0.2fr 0.3fr 0.3fr 0.8fr ;
				margin: 15px 10px;
			}
			@media screen and (max-width:575px){
				grid-template-columns: 0.5fr 0.7fr 1fr ;
				grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr; 
			}
			#title{
				grid-column: 1/7;
				grid-row: 1/2;
				@media screen and (max-width:575px){
					grid-column: 1/4;
					grid-row: 1/2;
				}
			}
			#percent{
				grid-column: 1/2;
				grid-row: 2/3;
				@media screen and (max-width:575px){
					grid-column: 1/2;
					grid-row: 2/3;
				}
			}
			#timecontributions{
				white-space: nowrap;
				grid-column: 2/4;
				grid-row: 2/3;
				@media screen and (max-width:575px){
					grid-column: 2/3;
					grid-row: 2/3;
				}
			}
			#minimalcontributions{
				grid-column: 5/7;
				grid-row: 2/3;
				@media screen and (max-width:575px){
					grid-column: 3/4;
					grid-row: 2/3;
				}
			}
			#checks1{
				display: flex;
				align-items: center;
				grid-column: 1/3;
				grid-row: 3/4;
				@media screen and (max-width:575px){
					grid-column: 1/3;
					grid-row: 4/5;
				}
			}
			#checks2{
				display: flex;
				align-items: center;
				grid-column: 3/5;
				grid-row: 3/4;
				@media screen and (max-width:575px){
					grid-column: 3/4;
					grid-row: 3/4;
				}
			}
			#checks3{
				display: flex;
				align-items: center;
				grid-column: 6/7;
				grid-row: 3/4;
				@media screen and (max-width:575px){
					grid-column: 1/3;
					grid-row: 3/4;
				}
			}
			#btn1{
				grid-column: 1/5;
				grid-row: 4/5;
				@media screen and (max-width:575px){
					grid-column: 1/4;
					grid-row: 6/7;
				}

			}
			#btn2{
				grid-column: 4/7;
				grid-row: 4/5;
				justify-self: end;
				@media screen and (max-width:575px){
					grid-column: 1/4;
					grid-row: 5/6;
					margin-left: 0;
					margin-bottom: 15px;
					justify-self: inherit;
				}
			}
		}

		// .contributions-type__title

		&__title {
			font-family: 'Helvetica';
			font-style: normal;
			font-weight: 700;
			font-size: 24px;
			line-height: 28px;
			background: linear-gradient(55.92deg, #011C4F 13.42%, rgba(1, 28, 79, 0.85) 49.7%, rgba(1, 28, 79, 0.7) 92.96%, rgba(1, 28, 79, 0.5) 121.27%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			background-clip: text;
			text-fill-color: transparent;
		}
	   
		&__list {
			display: flex;
			flex-direction: row;
			
		}

		// .contributions-type__item

		&__item {
			padding: 6px 12px;
			background: #FFFFFF;
			border: 0.5px solid rgba(216, 215, 215, 0.59);
			&:not(:last-child){
				margin-right: 20px;
				@media screen and (max-width:575px){
					margin-right: 10px;
				}
			}
		}

		// .contributions-type__symbhol

		&__symbhol {
			width: 15px;
			height: 23px;
			fill: #7A7A7A;
		}


		// .contributions-type__comtainer

		
		// .contributions-type__text

		&__text {
			font-family: 'Helvetica';
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 18px;
			color: #4E4E4E;
			@media screen and (max-width: 767px){
				font-size: 14px;
				line-height: 16px;
			}
		}

		// .contributions-type__description

		&__description {
			font-family: 'Helvetica';
			font-style: normal;
			font-weight: 700;
			font-size: 22px;
			line-height: 25px;
			color: #00205B;
			@media screen and (max-width: 767px){
				font-size: 18px;
				line-height: 21px;
			}
		}
}
.maximum-wrap{
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}
.contributions-type-picture {
	margin-left: 10px;
}
.btn.btn-contributions{
	@media screen and (max-width:1440px){
		width: -webkit-fill-available;
	}
}
.btn.btn-contributions.no-active{
	border: 1px solid #4E4E4E;
	border-radius: 2px; 
	color: #7A7A7A;
	background: transparent;
}
.contributions-filters{
	border: 1px solid rgba(133, 133, 133, 0.1);
	padding: 30px 60px;
	margin-bottom: 40px;
	@media screen and (max-width: 767px) {
		padding: 0;
		border: none;
	}
	
}
